body{ background: #f3f3f3;}

.music_con{ -ms-overflow-style: none; } .music_con::-webkit-scrollbar{ display:none; }


.top_con ,.nav_con{width: 648px;height: 70px;background: #fff;padding: 0 20px;box-sizing: border-box;display: flex;}
.title {color: #5B5C5E;font-size: 32px;position: absolute;top: 50%;left: 50%;transform: translate(-50%,-50%);}
.close{position: absolute;width: 60px;height: 60px;top: 50%;transform: translateY(-50%);}
.close::after,.close::before{content: '';width: 36px;height: 2px;background: #5B5C5E;display: block;position: absolute;top: 50%;left: 50%;transform: translate(-50%, -50%);}
.close::after{transform: translate(-50%, -50%) rotate(45deg);}
.close::before{transform: translate(-50%, -50%) rotate(-45deg);}
.top_con{position: relative;}
.nav_con{ border-bottom: 1px solid #E6E6E6;align-items: center;}
.nav_con p {color: #838181;text-align: center;letter-spacing: -1px;font-size: 22px;font-weight: bold;height: 70px;line-height: 70px;}
.nav_con p:nth-child(1){width: 30%;}
.nav_con p:nth-child(2){color: #0096FF;width: 40%;border-bottom: 4px solid #0096FF;
    box-sizing: border-box;}
.nav_con p:nth-child(3){width: 30%;}

.box { width: 648px;height: 70px;background: #fff;padding: 0 20px;box-sizing: border-box;display: flex;}



.list_con{padding: 0 20px;background:#fafafa;}
.list_con .music_box {width: 100%;height: 90px;padding: 10px 0;display: flex; transition: 0.2s; }
.list_con .music_box:nth-child(1){padding: 20px 0 10px;}

.click {box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 15%);border-radius: 10px; transition: 0.2s; padding: 10px 10px!important;
    box-sizing: border-box;
    height: 110px!important;}

.music_img {margin-top: 5px;box-sizing: border-box;width: 80px;height: 80px;overflow: hidden;;border-radius: 20px;}
.music_text{text-align: left;width: 528px;padding: 10px 0px 10px 20px;box-sizing: border-box;}
.music_text div {display: inline-block; width: 440px;}
.name{font-size: 24px;padding: 8px 0px 10px;font-weight: 600;}
.text{font-size: 18px;}
.time {display: inline-block;}


.footer{margin: 0 auto;width: 648px;}
.music_con {width: 648px;height: 840px; overflow-y: scroll;
background: #fff;margin: 0 auto;position: relative;}
.play_con{width: 648px;height: 110px;background: #cdcccc;padding:20px;box-sizing: border-box;display: flex;justify-content: space-between;
align-items: center;}



.active {display: none;}
.opacity{opacity: 1!important;transition: 0.5s;}
.faPlay ,.faStop{width: 50px;height: 50px;transform: translate(-50%,-50%);position: absolute;}
.faPrev ,.faNext{width: 40px;height: 40px;vertical-align: unset;}




.music_img_con{width: 65px;height: 65px;border-radius: 10px;background: red;}
.music_detail{width: 65px;height: 65px;background: red;}
#progress{width: 100%;margin: 0;height: 10px;opacity: 0;position: relative;z-index: 2;top: -9px;}

.progressbar_con{width: 100%;height: 0;}

